export default {
    getMsg(state, data) {
        state.movies = data;
    },
    scrollMore(state, preload) {
        let { data, skipNum } = preload;
        console.log(preload)
        state.movies = preload.data;
        state.baseNum =preload.skipNum;
    }
}


