<template>
  <div class="swiper-container">
    <mt-swipe :auto="4000" >
      <mt-swipe-item v-for="(item,i) in list" :key="i">
        <img :src="item" @click="goIndex(i)">
      </mt-swipe-item>
    </mt-swipe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1526564334&di=fc17516de94173efbc16d9280b38a5e5&imgtype=jpg&er=1&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F01b2295568a994000001271604da4f.jpg%401280w_1l_2o_100sh.jpg",
        "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525969617991&di=008a41a8f66b2cc87002bc06dab2b81c&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F0178fc57a199730000012e7e0b9559.jpg%401280w_1l_2o_100sh.jpg",
        "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525969617990&di=bed8661f735c545c71a4cb19d2cfcfa0&imgtype=0&src=http%3A%2F%2Fimg3.duitang.com%2Fuploads%2Fitem%2F201511%2F29%2F20151129194142_hZzMP.jpeg"
      ]
    };
  },
  methods: {
    goIndex(i) {
      if (i == 2) {
        this.$router.push("app/home");
      }
    }
  },
  created() {
    var count = 0;
    if (!localStorage.count) {
      localStorage.setItem("count", 0);
    } else {
      count++;
      localStorage.setItem("count", count);
      this.$router.push("app/home");
    }
  }
};
</script>

<style lang="scss" scoped>
.swiper-container {
  height: 100%;
  img {
    width: 100%;
    height: auto;
  }
}
</style>







