import Vue from "vue";
import axios from "axios";
import { Toast } from "mint-ui";
import state from "../vuex/state";
var { baseNum } = state;
axios.defaults.baseURL = 'http://localhost:2000/vue';

export default {
    getMsg({ commit }, url) {
        axios.get(url)
            .then(res => res.data)
            .then(data => {
                console.log(data);
                Toast({
                    message: '',
                    duration: 1000
                });
                setTimeout(function () {
                    commit("getMsg", data)
                }, 1000)
            })
    },
    scrollMore({ commit }, url) {
        let skipNum = baseNum + 10;
        console.log(skipNum);
        axios.get(url, {
            params: {
                skipNum
            }
        })
            .then(res => res.data)
            .then(data => {
                commit("scrollMore", {
                    preload: {
                        data, skipNum
                    }
                })
            })
    }
}


