<template>
    <mt-navbar fixed>
      <mt-tab-item v-for="(item,key,i) in list" :key="i">
        <router-link :to={path:item.path,name:item.name} activeClass="active">
          <span>{{item.txt}}</span>
        </router-link>
      </mt-tab-item>
    </mt-navbar>
</template>

<script>
export default {
  data() {
    return {
      list: [
        { name: "home", path: "/app/home", txt: "" },
        { name: "buy", path: "/app/buy", txt: "" },
        { name: "find", path: "/app/mall", icon: "icon-faxian", txt: "" },
        { name: "mine", path: "/app/find", icon: "icon-mine", txt: "" },
        { name: "mine", path: "/app/mine", icon: "icon-mine", txt: "" }
      ]
    };
  }
};
</script>
<style lang="scss" scoped>
a {
  color: #aaa;
}
.active {
  border-bottom: 1px solid #fff;
}
</style>






