<template>
    <div class="wrapper">
        <mt-search
            cancel-text=""
            placeholder=""
            v-model="value"
            >
        </mt-search>
        <div class="cancel" @click="goback"></div>
        <div class="searchBtn" @click="getlist"></div>
        <p>{{value}}</p>
    </div>
</template>

<script>
export default {
    data(){
        return{
            value:""
        }
    },
    methods:{
        goback(){
            this.$router.go(-1);
        },
        getlist(){
        //    axios.get('http://47.94.208.182:3000/movie',{searchVal:this.value})
        //    .then(res=>res.data)
        }
    }
};
</script>
<style lang="scss" scoped>
$bw: 50px;
$bh: 50px;
.cancel,
.searchBtn {
  position: absolute;
  z-index: 100;
}
.cancel {
  width: 0.8rem;
  height: 0.7rem;
  top: 0.08rem;
  right: 0;
}
.searchBtn {
  width: 0.4rem;
  height: 0.6rem;
  left: 0.1rem;
  top: 0.14rem;
}
</style>






