<template>
  <div class="search">
      <input type="text" placeholder=""  @click="goSearch()">
      <i class="iconfont icon-sousuo"></i>
  </div>
</template>
<script>
import router from "../router";
export default {
    methods:{
        goSearch(){
            this.$router.push('/search');
        }
    }
};
</script>
<style lang="scss" scoped>
.search {
  position: relative;
  display: flex;
  height: 65px;
  justify-content: center;
  align-items: center;
  background: #eae7e7;
  input {
    width: 610px;
    height: 50px;
    border-radius: 8px;
    text-indent: 50%;
  }
  .iconfont {
    position: absolute;
    left: 45%;
    top: 50%;
    transform: translateY(-50%);
  }
}
</style>





