<template>
  <div 
      class="section"
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10">
    <Searchbar/>
    <div class="card" v-for="(item,i) in movies" :key="i">
      <div class="portrait">
        <img :src="item.images.small" alt="">
      </div>
      <div class="detail">
        <h3 class="name">{{item.title}}</h3>
        <div class="record">{{item.year}}</div>   
      </div> 
    </div>
  </div>
</template>
<script>
import Searchbar from "../components/Searchbar.vue";
import { mapState, mapActions } from "vuex";
import { InfiniteScroll } from "mint-ui";
export default {
  computed: {
    ...mapState(["movies"])
  },
  components: {
    Searchbar
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    ...mapActions(["getMsg","scrollMore"]),
    loadMore() {
      this.scrollMore('/scrollMore')
      // console.log(111)
      // this.loading = true;
      // setTimeout(() => {
      //   // let last = this.msgList[this.msgList.length - 1];
      //  this.scrollMore('/scrollMore');
      //   // this.msgList.push(last + i);
      //   this.loading = false;
      // }, 5500);
    }
  },
  mounted() {
    this.getMsg("/getMsg");
  }
};
</script>
<style lang="scss" scoped>
.card {
  display: flex;
  padding: 0.15rem 0;
  margin-left: 0.18rem;
  border-bottom: 1px solid #eee;
  .portrait {
    width: 80px;
    height: 80px;
    background: red;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .detail {
    flex: auto;
    display: flex;
    flex-direction: column;
    text-indent: 15px;
    .name {
      flex: 1;
      font-size: larger;
      height: 25px;
    }
    .record {
      flex: 1;
      height: 25px;
    }
  }
}
</style>



